// ui effects
const closeCookieBtn = document.querySelector('#js-close-cookie');
const cookieBlock = document.querySelector('.cookie');

closeCookieBtn.addEventListener('click', () => {
  cookieBlock.classList.add('hidden');
});

const showMessageOnDataSent = (node, message) => {
  const box = node;
  box.querySelector('button').disabled = true;
  box.querySelector('button').textContent = message;
};

const sendDatatoServerFromForm = (node, data, url) => {
  fetch(url, {
    headers: {
      'content-type': 'application/x-www-form-urlencoded',
    },
    method: 'POST',
    body: data,
  })
    .then(() => {
      showMessageOnDataSent(node, 'Заявка отправлена');
      Metric.goal(node.getAttribute('data-form'));
    })
    .catch(() => {
      showMessageOnDataSent(node, 'Ошибка. Обновите страницу');
    });
};

// add submit events to all forms
[].slice.call(document.querySelectorAll('form')).forEach((item) => {
  item.addEventListener('submit', (e) => {
    e.preventDefault();
    const formData = new FormData(e.target);
    const object = {};
    formData.forEach((value, key) => {
      object[key] = value;
    });
    const json = JSON.stringify(object);

    sendDatatoServerFromForm(e.target, json, e.target.action);
  });
});

// find all metric elements
[].slice.call(document.querySelectorAll('[data-goal]')).forEach((item) => {
  item.addEventListener('click', () => {
    Metric.goal(item.getAttribute('data-goal'));
  });
});

// add metric goal if user spen 20sec or more on site
setTimeout(() => {
  Metric.goal('20sec');
}, 20000);



